$colorPrimary: #8739f9;
$colorAcccent: #37B9f1;
$colorGrey: #F2F5F5;
$colorText: #565360;
$colorLabel: #908E9b;
$colorDisabled: #e1efe9;
$colorHover: #c651cd;

.profileCard{
    background-color: $colorGrey;
    max-width: 20rem;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    border-radius: 2rem;
    box-shadow: 0px 1rem 1.5rem rgba(black, 0.5);

    &__header {
        width: 100%;
        height: 11rem;
        background-color: $colorPrimary;
        display: flex;
        align-items: flex-end;
        justify-content: center; 
    }
  
    &__body{
        margin-top: 5rem;
        width: 100%;
        padding: 0 1rem;
   
    }
    &__name{
        text-align: center;
        font-size: 1.5rem;
        color: $colorText;
        padding: 0.5rem 0;
    }
    &__status{
        width: 80%;
        height: 3rem;
        margin: 0 auto;
        text-align: center;
        font-size: 1rem;
        color: $colorText;
        padding: 0.5rem 0;
        overflow: scroll;
        &_noStatus{
            color: $colorLabel
        }
        &_input{
            width: 80%;
            height: 2rem;
            border-radius: 1rem;
            border: none;
            background-color: #fff;
            padding: 0.1rem;
           
        }
    }
    &__social{
        padding: 0.5rem 2rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__socialIcon{
        width: 1.9rem;
        height: 1.9rem;
        img{
            width: 100%;
        }
    }
    &__footer{
        padding: 2rem 2rem;
        display: flex;
        justify-content: center;
    }
    &__btn{
        button {
            color: $colorGrey;
            background-color: $colorPrimary;
            font-size: 1rem;
            border: none;
            font-weight: bold;
            padding: 1rem 4rem;
            border-radius: 1.2rem;
            transition: color 0.9s ease-in-out;
            &:hover{
                background-color: $colorHover;
            }
        }
    }
    

}