$colorPrimary: #8739f9;
$colorGrey: #F2F5F5;
$colorLabel: #908E9b;

.header{
    grid-area: 1/1/2/5;
    background-color: $colorPrimary;
    color: $colorGrey;
    padding: 1rem 3rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    &__logo {
        font-size: 2rem;
    }
    &__avatar {
        width: 3rem;
        height: 3rem;
        border-radius: 100%;
        img{
            width: 100%;
            height: 100%;
            border-radius: 100%;
        }
    }
    &__login {
        font-size: 1rem;
        display: flex;
        min-width: 13rem;
        justify-content: space-between;
        align-items: center;
        
        a{
            text-decoration: none;
            color:#F2F5F5;
            font-weight: bold;
            &:hover{
                color: #c651cd;
                cursor: pointer;
            }
        }
    
}
}

.logout{
    text-decoration: none;
    color: $colorLabel;
    font-weight: bold;
    &:hover{
        color: #c651cd;
        cursor: pointer;
    }
}

.unknown{
    color: $colorLabel;
    font-weight: bold;
}