@import '../App/variables.scss';

.dialogs {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.messages {
    padding: 2rem;
}

.message {
    background-color: $colorGrey;
    border-radius: 2rem;
    box-shadow: 0px 1rem 1.5rem rgba(black, 0.5);
    display:grid;
    grid-template-columns: 5rem 1fr 5rem;
    padding: 1rem;
    align-items: center;
    &__delete{
        background-color: $colorPrimary;
        width: 1.9rem;
        height: 1.9rem;
        border-radius: 50%;
        line-height: 1.9rem;
        color: $colorGrey;
        font-size: 1.1rem;
        text-align: center;
    }

}