$colorPrimary: #8739f9;
$colorAcccent: #37B9f1;
$colorGrey: #F2F5F5;
$colorText: #565360;
$colorLabel: #908E9b;
$colorDisabled: #e1efe9;
$colorHover: #c651cd;

.users{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.userCard{
    margin-top: 2rem;
    background-color: $colorGrey;
    width: 35rem;
    height: 10rem;
    display: flex;
    border-radius: 1.2rem;
    box-shadow: 0px 1rem 1.5rem rgba(black, 0.5);
    justify-content: space-between;

    &__header {
        width: 8rem;
        border-radius: 1.2rem 0 0 1.2rem;
        height: auto;
        background-color: $colorPrimary;
        display: flex;
        align-items: center;
        justify-content: center; 
    }
    &__avatar {
        width: 5rem;
        height: 5rem;
        border-radius: 100%;
        img{
            width: 100%;
            height: 100%;
            border-radius: 100%;
        }
    }
    &__body{
        width: 17rem;
        padding: 1rem 2rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        color: $colorText;
    }

    &__name{
        font-size: 1.5rem; 
    }
    &__status{
        font-size: 1rem;
        margin-top: 0.5rem;
    }
    &__social{
        padding: 0.5rem 2rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__socialIcon{
        width: 1.5rem;
        height: 1.5rem;
        img{
            width: 100%;
        }
    }
    &__footer{
        width: 10rem;
        display: flex;
        justify-content: center;
        align-items:center;
    }
    &__btn{
        width: 10rem;
        height: 10rem;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items:center;
        padding: 1.2rem 0;
        
        button {
            color: $colorGrey;
            background-color: $colorPrimary;
            font-size: 1rem;
            border: none;
            font-weight: bold;
            width: 7rem;
            height: 2.5rem;
            border-radius: 1.2rem;
            transition: color .7s ease-in-out;
            &:disabled{
                background-color: #908E9b;
                &:hover{
                    background-color: #908E9b;
                }
            }
            &:hover{
                background-color: $colorHover;
                cursor: pointer;
            }
            &:nth-of-type(2){
                background-color: $colorAcccent;
            }
            
        }
    }
    

}


