$colorPrimary: #8739f9;
$colorAcccent: #37B9f1;
$colorGrey: #F2F5F5;
$colorText: #565360;
$colorLabel: #908E9b;
$colorDisabled: #e1efe9;
$colorHover: #c651cd;

.followBtn{
    width: 10rem;
    height: 10rem;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items:center;
    padding: 1.2rem 0;
    
    button {
        color: $colorGrey;
        background-color: $colorPrimary;
        font-size: 1rem;
        border: none;
        font-weight: bold;
        width: 7rem;
        height: 2.5rem;
        border-radius: 1.2rem;
        transition: color .7s ease-in-out;
        &:disabled{
            background-color: #908E9b;
            &:hover{
                background-color: #908E9b;
            }
        }
        &:hover{
            background-color: $colorHover;
            cursor: pointer;
        }
        &:nth-of-type(2){
            background-color: $colorAcccent;
        }
        
    }
}