$colorPrimary: #8739f9;
$colorAcccent: #37B9f1;
$colorGrey: #F2F5F5;
$colorText: #565360;
$colorLabel: #908E9b;
$colorDisabled: #e1efe9;
$colorHover: #c651cd;

.pagination {
    display: flex;
    flex-wrap: wrap;
    &__btn{
        font-size: 0.9rem;
        font-weight: bold;
        width: 2.2rem;
        height: 2.2rem;
        line-height: 2.2rem;
        text-align: center;
        background-color: $colorPrimary;
        color: $colorGrey;
        margin: 1rem;
        border-radius: 0.3rem;
        &:hover{
            background-color: $colorHover;
        }
        
    }
}

.current{
    background-color: $colorAcccent;
}

.next, .prev{
    font-size: 0.9rem;
    font-weight: bold;
    width: 3.2rem;
    height: 2.2rem;
    line-height: 2.2rem;
    text-align: center;
    background-color: $colorPrimary;
    color: $colorGrey;
    margin: 1rem;
    border-radius: 0.3rem;
    border: none;
    &:hover{
        background-color: $colorHover;
    }
    &:disabled{
        background-color: #908E9b;
    }
}