@import '../../App/variables.scss';


.form{
    background-color: $colorGrey;
    min-width: 40rem;
    display: grid;
    grid-template-columns: 1fr 10rem;
    border-radius: 2rem;
    box-shadow: 0px 1rem 1.5rem rgba(black, 0.5);
    padding: 3rem;
    margin: 2rem 0;
    &__btn{
        height: 4rem;
        color: $colorGrey;
        background-color: $colorPrimary;
        font-size: 1rem;
        border: none;
        font-weight: bold;
        padding: 1rem;
        border-radius: 1.2rem;
        transition: color 0.9s ease-in-out;
        &:hover{
            background-color: $colorHover;
        } 
    }
}
.post {
    background-color: $colorGrey;
    min-width: 40rem;
    display: grid;
    grid-template-columns: 10rem 1fr;
    border-radius: 2rem;
    box-shadow: 0px 1rem 1.5rem rgba(black, 0.5);
    padding: 3rem 0;
    margin: 2rem 0;
    
    &__text {
        color: $colorText;
        justify-self: start;
        padding: 0 2rem;
    }
    &__avatar{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    
}
