@import '../App/variables';


.avatar {
    width: 3rem;
    height: 3rem;
    border-radius: 100%;
    img{
        width: 100%;
        height: 100%;
        border-radius: 100%;
    }
}

.big {
    width: 8rem;
    height: 8rem;
    border-radius: 100%;
    transform: translateY(50%);
    transition: transform 1s ease-in-out;
    &:hover{
        transform: translateY(50%) scale(1.1);
    }

    img {
        width: 100%;
        height: 100%;
        border-radius: 100%;
    }
}

.middle {
    width: 5rem;
    height: 5rem;
    border-radius: 100%;
    border: $colorPrimary 3px solid;

    img {
        width: 100%;
        height: 100%;
        border-radius: 100%;
    }
}

.small {
    width: 3rem;
    height: 3rem;
    border-radius: 100%;
    border: $colorPrimary 2px solid;

    img {
        width: 100%;
        height: 100%;
        border-radius: 100%;
    }
}