$colorLabel: #908E9b;
$colorGrey: #F2F5F5;
.active{
    color: #37B9f1 !important;
}
.nav{
    grid-area: 2/1/4/2;
    background-color: #1B1B1B;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    &__section{
        color: $colorLabel;
        padding: 2rem 0;
    }
    &__link {
        font-size: 1rem;
        font-weight: bold;
        text-decoration: 0;
        color: $colorLabel;
        &:hover{
            color: #c651cd;
            cursor: pointer;
        }
    }
}