@import '../App/variables.scss';

.postForm {
    textarea{
        width: 95%;
        height: 4rem;
        padding: 1rem;
        background-color: $colorGrey;
        border: none;
        border-radius: 1.2rem;
        border: 1px $colorLabel solid;
    }
    span{
        font-size: 1rem;
        color: $error;
    }
   
}

.error{
    textarea{
        background-color: $errorSoft;
        border: none;
        border: 2px $error solid;
        width: 90%;
        height: 4rem;
        padding: 1rem;
        border-radius: 1.2rem;
        
    }
    span{
        font-size: 1rem;
        color: $error;
    }
}
